import { useState, useRef, useEffect } from 'react'

const DynamicNavigation = ({ isSticky, page }) => {
    return (
        <>
        <div className="nav_sticky navigation-links">
            <a>back</a>
            <div className={`navigation-links__page ${!isSticky ? 'disappear' : 'appear'}`}>{page}</div>
            <p>next</p>
        </div>
        </>
    )
}

export default DynamicNavigation